.wrapper {
  width: 100px;
  height: 200px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.input {
  display: none;
}

.label {
  width: 100%;
  height: 50%;
  top: 25%;
  z-index: -1;

  &--active {
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.05);
    z-index: 2;
  }
}

.img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: auto;
}